import React, { useState, useEffect } from 'react';
import axios from 'axios';
import * as XLSX from 'xlsx';
import './KhaiBaoCoSo.css';
import speciesOptions from '../data/speciesData';
import { jwtDecode } from 'jwt-decode';
import { useNavigate } from 'react-router-dom';

function KhaiBaoCoSo() {
  const navigate = useNavigate();

  const initialFormState = {
    tenCoSo: '', tinhThanhPho: '', xaPhuong: '', diaChiCoSo: '', vido: '', kinhdo: '',
    ngayThanhLap: '', giayPhepKinhDoanh: '',
    tenNguoiDaiDien: '', namSinh: '', soCCCD: '', ngayCapCCCD: '', noiCapCCCD: '',
    soDienThoaiNguoiDaiDien: '', diaChiNguoiDaiDien: '', emailNguoiDaiDien: '',
    mucDichNuoi: '', hinhThucNuoi: '', maSoCoSoGayNuoi: '', tongDan: '',
    loaiHinhKinhDoanhGo: '', nganhNgheKinhDoanhGo: '', khoiLuong: '',
    loaiHinhCheBienGo: '', nguonGocGo: '',
    loaiCoSoDangKy: '',
    tenLamSan: '', tenKhoaHoc: '',
    issueDate: '', expiryDate: '',
    trangThai: 'ang hot ng',
  };

  const [formData, setFormData] = useState(initialFormState);
  const [loading, setLoading] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });
  const [csvFile, setCsvFile] = useState(null);
  const [excelData, setExcelData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const [openSections, setOpenSections] = useState({
    thongTinCoSo: true,
    nguoiDaiDien: true,
    loaiCoSo: true,
    thongTinLoaiHinh: false,
    giayPhep: true,
    upload: true,
  });

  const toggleSection = (sectionName) => {
    setOpenSections(prev => ({ ...prev, [sectionName]: !prev[sectionName] }));
  };
  
  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      try {
        if (jwtDecode(token).role === 'admin') setIsAdmin(true);
      } catch (err) {
        localStorage.removeItem('token');
        navigate('/login');
      }
    } else {
      navigate('/login');
    }
  }, [navigate]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const resetForm = () => {
    setFormData(initialFormState);
    setMessage({ type: '', text: '' });
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ type: '', text: '' });
    const token = localStorage.getItem('token');
    if (!token) { return; }

    try {
        const submissionData = { ...formData };
        submissionData.products = [];
        
        if (submissionData.loaiCoSoDangKy === 'ng k c s gy nui' && submissionData.tenLamSan) {
            submissionData.products.push({
                tenLamSan: submissionData.tenLamSan,
                tenKhoaHoc: speciesOptions.find(s => s.name === submissionData.tenLamSan)?.scientificName || '',
                khoiLuong: submissionData.tongDan,
                donViTinh: 'c th',
                mucDichNuoi: submissionData.mucDichNuoi,
                hinhThucNuoi: submissionData.hinhThucNuoi,
                maSoCoSoGayNuoi: submissionData.maSoCoSoGayNuoi
            });
        } else if (submissionData.loaiCoSoDangKy === 'ng k c s kinh doanh, ch bin g' && submissionData.tenLamSan) {
            submissionData.products.push({
                tenLamSan: submissionData.tenLamSan,
                tenKhoaHoc: submissionData.tenKhoaHoc,
                khoiLuong: submissionData.khoiLuong,
                donViTinh: 'm',
                loaiHinhCheBienGo: submissionData.loaiHinhCheBienGo,
                nguonGocGo: submissionData.nguonGocGo
            });
        }
        
        const fieldsToDelete = ['tenLamSan', 'tenKhoaHoc', 'khoiLuong', 'tongDan', 'loaiHinhCheBienGo', 'nguonGocGo', 'mucDichNuoi', 'hinhThucNuoi', 'maSoCoSoGayNuoi'];
        fieldsToDelete.forEach(field => delete submissionData[field]);

        await axios.post('http://localhost:10000/api/farms', submissionData, {
            headers: { Authorization: `Bearer ${token}` }
        });
        setMessage({ type: 'success', text: 'ng k c s thnh cng!' });
        resetForm();
    } catch (err) {
        setMessage({ type: 'error', text: err.response?.data?.message || 'ng k tht bi.' });
    } finally {
        setLoading(false);
    }
  };

  const handleFileUpload = (e) => { /* ... */ };
  const handleBulkSubmit = async () => { /* ... */ };
  const resetCsvUpload = () => { /* ... */ };

  return (
    <div className="khai-bao-container">
      <h2>ng k c s</h2>
      {message.type && <div className={`message ${message.type}`}>{message.text}<button onClick={() => setMessage({ type: '', text: '' })}>x</button></div>}
      
      <form onSubmit={handleSubmit} className="khai-bao-form">
        <section className="khai-bao-section">
          <h3 onClick={() => toggleSection('thongTinCoSo')} className="section-title-clickable">Thng tin c s {openSections.thongTinCoSo ? '' : ''}</h3>
          {openSections.thongTinCoSo && (
            <>
                <div className="form-group"><label>Tn c s:</label><input type="text" name="tenCoSo" value={formData.tenCoSo} onChange={handleChange} required /></div>
                <div className="form-group"><label>Tnh (Thnh ph):</label><input type="text" name="tinhThanhPho" value={formData.tinhThanhPho} onChange={handleChange} required /></div>
                <div className="form-group"><label>X (Phng):</label><input type="text" name="xaPhuong" value={formData.xaPhuong} onChange={handleChange} required /></div>
                <div className="form-group"><label>a ch c s:</label><input type="text" name="diaChiCoSo" value={formData.diaChiCoSo} onChange={handleChange} required /></div>
                <div className="form-group"><label>V :</label><input type="number" name="vido" value={formData.vido} onChange={handleChange} step="any" /></div>
                <div className="form-group"><label>Kinh :</label><input type="number" name="kinhdo" value={formData.kinhdo} onChange={handleChange} step="any" /></div>
                <div className="form-group"><label>Ngy thnh lp:</label><input type="date" name="ngayThanhLap" value={formData.ngayThanhLap} onChange={handleChange} /></div>
                <div className="form-group"><label>S giy php kinh doanh:</label><input type="text" name="giayPhepKinhDoanh" value={formData.giayPhepKinhDoanh} onChange={handleChange} /></div>
            </>
          )}
        </section>

        <section className="khai-bao-section">
          <h3 onClick={() => toggleSection('nguoiDaiDien')} className="section-title-clickable">Thng tin ngi i din {openSections.nguoiDaiDien ? '' : ''}</h3>
          {openSections.nguoiDaiDien && (
             <>
                <div className="form-group"><label>Tn ngi i din:</label><input type="text" name="tenNguoiDaiDien" value={formData.tenNguoiDaiDien} onChange={handleChange} required /></div>
                <div className="form-group"><label>Nm sinh:</label><input type="number" name="namSinh" value={formData.namSinh} onChange={handleChange} /></div>
                <div className="form-group"><label>S CCCD/H chiu:</label><input type="text" name="soCCCD" value={formData.soCCCD} onChange={handleChange} required /></div>
                <div className="form-group"><label>Ngy cp CCCD:</label><input type="date" name="ngayCapCCCD" value={formData.ngayCapCCCD} onChange={handleChange} /></div>
                <div className="form-group"><label>Ni cp CCCD:</label><input type="text" name="noiCapCCCD" value={formData.noiCapCCCD} onChange={handleChange} /></div>
                <div className="form-group"><label>S in thoi ngi i din:</label><input type="tel" name="soDienThoaiNguoiDaiDien" value={formData.soDienThoaiNguoiDaiDien} onChange={handleChange} /></div>
                <div className="form-group"><label>a ch ngi i din:</label><input type="text" name="diaChiNguoiDaiDien" value={formData.diaChiNguoiDaiDien} onChange={handleChange} /></div>
                <div className="form-group"><label>Email ngi i din:</label><input type="email" name="emailNguoiDaiDien" value={formData.emailNguoiDaiDien} onChange={handleChange} /></div>
            </>
          )}
        </section>
        
        <section className="khai-bao-section">
          <h3 onClick={() => toggleSection('loaiCoSo')} className="section-title-clickable">Loi c s ng k {openSections.loaiCoSo ? '' : ''}</h3>
          {openSections.loaiCoSo && (
            <div className="form-group">
              <label htmlFor="loaiCoSoDangKy">Chn loi c s ng k:</label>
              <select id="loaiCoSoDangKy" name="loaiCoSoDangKy" value={formData.loaiCoSoDangKy} onChange={handleChange} required>
                <option value="">-- Chn loi --</option>
                <option value="ng k c s gy nui">C s gy nui (ng vt/Thc vt)</option>
                <option value="ng k c s kinh doanh, ch bin g">C s kinh doanh, ch bin g</option>
              </select>
            </div>
          )}
        </section>

        {formData.loaiCoSoDangKy === 'ng k c s gy nui' && (
          <section className="khai-bao-section">
            <h3 onClick={() => toggleSection('thongTinGayNuoi')} className="section-title-clickable">Thng tin c s gy nui {openSections.thongTinGayNuoi ? '' : ''}</h3>
            {openSections.thongTinGayNuoi && (
              <>
                <div className="form-group"><label>Mc ch nui:</label><input type="text" name="mucDichNuoi" value={formData.mucDichNuoi} onChange={handleChange} /></div>
                <div className="form-group"><label>Hnh thc nui:</label><input type="text" name="hinhThucNuoi" value={formData.hinhThucNuoi} onChange={handleChange} /></div>
                <div className="form-group"><label>M s c s gy nui:</label><input type="text" name="maSoCoSoGayNuoi" value={formData.maSoCoSoGayNuoi} onChange={handleChange} /></div>
                <div className="form-group"><label>Tng n (con):</label><input type="number" name="tongDan" value={formData.tongDan} onChange={handleChange} /></div>
                <div className="form-group">
                  <label htmlFor="tenLamSan">Loi nui:</label>
                  <select id="tenLamSan" name="tenLamSan" value={formData.tenLamSan} onChange={handleChange}>
                    <option value="">-- Chn loi --</option>
                    {Array.isArray(speciesOptions) && speciesOptions.map((species, index) => (<option key={index} value={species.name}>{species.name}</option>))}
                  </select>
                </div>
                <div className="form-group">
                  <label htmlFor="tenKhoaHoc">Tn khoa hc (t ng in):</label>
                  <input type="text" value={Array.isArray(speciesOptions) ? (speciesOptions.find(s => s.name === formData.tenLamSan)?.scientificName || '') : ''} readOnly disabled />
                </div>
              </>
            )}
          </section>
        )}

        {formData.loaiCoSoDangKy === 'ng k c s kinh doanh, ch bin g' && (
          <section className="khai-bao-section">
             <h3 onClick={() => toggleSection('thongTinKinhDoanhGo')} className="section-title-clickable">Thng tin c s kinh doanh, ch bin g {openSections.thongTinKinhDoanhGo ? '' : ''}</h3>
             {openSections.thongTinKinhDoanhGo && (
                <>
                    <div className="form-group"><label>Loi hnh kinh doanh g:</label><input type="text" name="loaiHinhKinhDoanhGo" value={formData.loaiHinhKinhDoanhGo} onChange={handleChange} /></div>
                    <div className="form-group"><label>Ngnh ngh kinh doanh g:</label><input type="text" name="nganhNgheKinhDoanhGo" value={formData.nganhNgheKinhDoanhGo} onChange={handleChange} /></div>
                    <div className="form-group"><label>Khi lng (m):</label><input type="number" name="khoiLuong" value={formData.khoiLuong} onChange={handleChange} step="any" /></div>
                    <div className="form-group"><label>Loi hnh ch bin g:</label><select name="loaiHinhCheBienGo" value={formData.loaiHinhCheBienGo} onChange={handleChange}><option value="">-- Chn loi hnh --</option><option value="Trn">Trn</option><option value="X">X</option><option value="Thnh phm">Thnh phm</option></select></div>
                    <div className="form-group"><label>Ngun gc g:</label><select name="nguonGocGo" value={formData.nguonGocGo} onChange={handleChange}><option value="">-- Chn ngun gc --</option><option value="Nhp khu">Nhp khu</option><option value="Vn">Vn</option><option value="Khc">Khc</option></select></div>
                    <div className="form-group"><label>Tn lm sn:</label><input type="text" name="tenLamSan" value={formData.tenLamSan} onChange={handleChange} /></div>
                    <div className="form-group"><label>Tn khoa hc:</label><input type="text" name="tenKhoaHoc" value={formData.tenKhoaHoc} onChange={handleChange} /></div>
                </>
             )}
          </section>
        )}

        <section className="khai-bao-section">
          <h3 onClick={() => toggleSection('giayPhep')} className="section-title-clickable">Thng tin giy php v trng thi {openSections.giayPhep ? '' : ''}</h3>
          {openSections.giayPhep && (
            <>
                <div className="form-group"><label>Ngy cp php:</label><input type="date" name="issueDate" value={formData.issueDate} onChange={handleChange} /></div>
                <div className="form-group"><label>Ngy ht hn:</label><input type="date" name="expiryDate" value={formData.expiryDate} onChange={handleChange} /></div>
                <div className="form-group"><label>Trng thi:</label><select name="trangThai" value={formData.trangThai} onChange={handleChange}><option value="ang hot ng">ang hot ng</option><option value=" ng ca"> ng ca</option><option value="Tm dng">Tm ngng</option></select></div>
                <div className="form-group"><label>Ghi ch:</label><textarea name="ghiChu" value={formData.ghiChu} onChange={handleChange} rows="3"></textarea></div>
            </>
          )}
        </section>
        
        <div className="form-actions">
          <button type="submit" className="submit-button" disabled={loading}>{loading ? 'ang gi...' : 'ng k c s'}</button>
          <button type="button" onClick={resetForm} className="reset-button" disabled={loading}>t li biu mu</button>
        </div>
      </form>

      {isAdmin && (
        <section className="khai-bao-section excel-upload-section">
           <h3 onClick={() => toggleSection('upload')} className="section-title-clickable">Ti ln hng lot t file CSV {openSections.upload ? '' : ''}</h3>
           {openSections.upload && (
            <>
              <div className="file-upload-group"><label htmlFor="csvUpload" className="file-upload-label">Chn file CSV (.csv):</label><input type="file" id="csvUpload" accept=".csv" onChange={handleFileUpload} className="file-input" />{csvFile && <span className="file-name">{csvFile.name}</span>}</div>
              {excelData.length > 0 && (<div className="excel-preview-info"><p> c c {excelData.length} dng d liu t CSV. Vui lng nhn "Ti ln hng lot"  lu.</p><button onClick={handleBulkSubmit} className="submit-button bulk-upload-button" disabled={isLoading}>{isLoading ? 'ang ti...' : `Ti ln hng lot (${excelData.length} c s)`}</button><button onClick={resetCsvUpload} className="clear-excel-button" disabled={isLoading}>Xa d liu CSV</button></div>)}
            </>
           )}
        </section>
      )}
    </div>
  );
}

export default KhaiBaoCoSo;