// src/pages/LoginPage.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './LoginPage.css'; // Import CSS cho trang ng nhp

function LoginPage({ setAuthStatus }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false); // State cho checkbox "Ghi nh ti khon"
  const [error, setError] = useState('');
  const navigate = useNavigate();

  // useEffect  ti thng tin ng nhp  lu khi component mount
  useEffect(() => {
    const savedEmail = localStorage.getItem('rememberedEmail');
    const savedPassword = localStorage.getItem('rememberedPassword');
    if (savedEmail && savedPassword) {
      setEmail(savedEmail);
      setPassword(savedPassword);
      setRememberMe(true); // t checkbox l true nu c thng tin  lu
    }
  }, []);

  const handleLogin = async (e) => {
    e.preventDefault();
    setError('');
    console.log('handleLogin function called.');

    try {
      const response = await axios.post('http://localhost:10000/api/auth/login', {
        email,
        password,
      });

      const { token, role } = response.data;

      setAuthStatus(token, role);

      // X l "Ghi nh ti khon"
      if (rememberMe) {
        localStorage.setItem('rememberedEmail', email);
        localStorage.setItem('rememberedPassword', password);
      } else {
        localStorage.removeItem('rememberedEmail');
        localStorage.removeItem('rememberedPassword');
      }

      console.log('Token after set (via setAuthStatus):', localStorage.getItem('token'));
      console.log('Role after set (via setAuthStatus):', localStorage.getItem('role'));

      navigate('/dashboard');
      console.log('Navigation to /dashboard attempted.');

    } catch (err) {
      console.error('Li ng nhp:', err);
      setError(err.response?.data?.message || 'ng nhp tht bi. Vui lng kim tra li email v mt khu.');
    }
  };

  return (
    <div className="login-page">
      <div className="login-container">
        <h2 className="login-title">ng nhp</h2>
        {error && <p className="login-error-message">{error}</p>}
        <form onSubmit={handleLogin}>
          <div className="login-form-group">
            <label htmlFor="email" className="login-label">E-mail</label>
            <input
              type="email"
              id="email"
              className="login-input"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          <div className="login-form-group">
            <label htmlFor="password" className="login-label">Mt khu</label>
            <input
              type="password"
              id="password"
              className="login-input"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <div className="login-remember-me">
            <input
              type="checkbox"
              id="rememberMe"
              checked={rememberMe}
              onChange={(e) => setRememberMe(e.target.checked)}
            />
            <label htmlFor="rememberMe">Ghi nh ti khon</label>
          </div>
          <button type="submit" className="login-button">ng nhp</button>
        </form>
       

        <div className="login-software-info" translate="no">
          Phin bn 1.0.0 <br />
          &copy;2025Nguyen Van Phuc&copy;0358.758.358&copy;Phuc88.klvn@gmail.com&copy;
        </div>
      </div>
    </div>
  );
}

export default LoginPage;
