import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import './KhaiBaoCoSo.css'; // Ti s dng CSS t form khai bo  giao din nht qun

function AddProductToFarm() {
  // Ly ID ca c s t thanh a ch URL (v d: /farm/abc-123/add-product)
  const { farmId } = useParams(); 
  const navigate = useNavigate();
  const token = localStorage.getItem('token');

  // State  lu thng tin c s cha (ch  hin th)
  const [farmInfo, setFarmInfo] = useState(null); 
  // State cho d liu ca form lm sn mi
  const [productData, setProductData] = useState({ 
    tenLamSan: '',
    tenKhoaHoc: '',
    khoiLuong: 0,
    donViTinh: 'm',
    loaiHinhCheBienGo: '',
    nguonGocGo: ''
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });

  // useEffect ny s chy mt ln khi trang c ti
  //  ly thng tin ca c s v hin th cho ngi dng
  useEffect(() => {
    if (!token) {
      navigate('/login');
      return;
    }
    
    const fetchFarmInfo = async () => {
      setLoading(true);
      try {
        const response = await axios.get(`http://localhost:10000/api/farms/${farmId}`, {
          headers: { Authorization: `Bearer ${token}` }
        });
        setFarmInfo(response.data);
      } catch (error) {
        console.error("Li khi ti thng tin c s:", error);
        setMessage({ type: 'error', text: 'Khng th ti thng tin c s. Vui lng quay li v th li.' });
      } finally {
        setLoading(false);
      }
    };

    fetchFarmInfo();
  }, [farmId, token, navigate]);

  // X l khi ngi dng thay i gi tr trong cc  input
  const handleChange = (e) => {
    const { name, value } = e.target;
    setProductData(prev => ({ ...prev, [name]: value }));
  };

  // X l khi ngi dng nhn nt "Lu Lm sn"
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ type: '', text: '' });

    try {
      // Gi n API endpoint mi  to  backend
      const response = await axios.post(`http://localhost:10000/api/farms/${farmId}/products`, productData, {
        headers: { Authorization: `Bearer ${token}` }
      });

      setMessage({ type: 'success', text: 'Thm lm sn thnh cng!' });
      
      // Reset form  ngi dng c th thm sn phm khc nu mun
      setProductData({
        tenLamSan: '', tenKhoaHoc: '', khoiLuong: '', donViTinh: 'm', loaiHinhCheBienGo: '', nguonGocGo: ''
      });
      
      // Ty chn: sau 2 giy, t ng chuyn v trang danh sch
      setTimeout(() => {
        navigate(-1); // Quay li trang trc  (trang danh sch)
      }, 2000);

    } catch (error) {
      console.error("Li khi thm lm sn:", error.response?.data || error.message);
      setMessage({ type: 'error', text: error.response?.data?.message || 'Thm lm sn tht bi. Vui lng kim tra li d liu.' });
    } finally {
      setLoading(false);
    }
  };

  if (loading && !farmInfo) {
    return <div className="khai-bao-container">ang ti thng tin c s...</div>;
  }

  return (
    <div className="khai-bao-container">
      <h2>Thm Lm sn mi cho C s</h2>

      {message.type && (
        <div className={`message ${message.type}`}>
          {message.text}
          <button onClick={() => setMessage({ type: '', text: '' })} className="close-message-button">x</button>
        </div>
      )}

      {/* Phn hin th thng tin c s - ch c, khng cho sa */}
      {farmInfo && (
        <section className="khai-bao-section read-only-section">
          <h3>Thng tin c s</h3>
          <p><strong>Tn c s:</strong> {farmInfo.tenCoSo}</p>
          <p><strong>a ch:</strong> {`${farmInfo.diaChiCoSo}, ${farmInfo.xaPhuong}, ${farmInfo.tinhThanhPho}`}</p>
          <p><strong>Ngi i din:</strong> {farmInfo.tenNguoiDaiDien}</p>
        </section>
      )}

      <hr />

      {/* Form  thm lm sn mi */}
      <form onSubmit={handleSubmit} className="khai-bao-form">
        <section className="khai-bao-section">
          <h3>Nhp thng tin lm sn</h3>
          
          <div className="form-group">
            <label htmlFor="tenLamSan">Tn lm sn:</label>
            <input type="text" id="tenLamSan" name="tenLamSan" value={productData.tenLamSan} onChange={handleChange} required />
          </div>

          <div className="form-group">
            <label htmlFor="tenKhoaHoc">Tn khoa hc:</label>
            <input type="text" id="tenKhoaHoc" name="tenKhoaHoc" value={productData.tenKhoaHoc} onChange={handleChange} />
          </div>

           <div className="form-group">
            <label htmlFor="khoiLuong">Khi lng:</label>
            <input type="number" id="khoiLuong" name="khoiLuong" value={productData.khoiLuong} onChange={handleChange} step="any" required />
          </div>

          <div className="form-group">
            <label htmlFor="donViTinh">n v tnh:</label>
            <input type="text" id="donViTinh" name="donViTinh" value={productData.donViTinh} onChange={handleChange} required />
          </div>

          <div className="form-group">
            <label htmlFor="loaiHinhCheBienGo">Loi hnh ch bin g:</label>
            <select id="loaiHinhCheBienGo" name="loaiHinhCheBienGo" value={productData.loaiHinhCheBienGo} onChange={handleChange} required>
              <option value="">-- Chn loi hnh --</option>
              <option value="Trn">Trn</option>
              <option value="X">X</option>
              <option value="Thnh phm">Thnh phm</option>
            </select>
          </div>

          <div className="form-group">
            <label htmlFor="nguonGocGo">Ngun gc g:</label>
            <select id="nguonGocGo" name="nguonGocGo" value={productData.nguonGocGo} onChange={handleChange} required>
              <option value="">-- Chn ngun gc --</option>
              <option value="Nhp khu">Nhp khu</option>
              <option value="Vn">Vn</option>
              <option value="Khc">Khc</option>
            </select>
          </div>

        </section>

        <div className="form-actions">
          <button type="submit" className="submit-button" disabled={loading}>
            {loading ? 'ang lu...' : 'Lu Lm sn'}
          </button>
          <button type="button" onClick={() => navigate(-1)} className="reset-button" disabled={loading}>
            Quay li
          </button>
        </div>
      </form>
    </div>
  );
}

export default AddProductToFarm;