// src/pages/Googlemaps.js
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';


import './Googlemaps.css'; // Import file CSS ring cho trang ny

/**
 * Component FarmListPage hin th danh sch cc c s nui,
 * cho php tm kim, lc, n/hin ct v phn trang.
 */
function FarmListPage() {
  // State  lu tr danh sch cc c s c ti v t API
  const [farms, setFarms] = useState([]);
  // State cho b lc tm kim chung (theo tn, a ch, ngi i din, v.v.)
  const [filter, setFilter] = useState('');
  // State cho b lc Tnh (Thnh ph)
  const [selectedProvince, setSelectedProvince] = useState('');
  // State cho b lc X (Phng)
  const [selectedCommune, setSelectedCommune] = useState('');
  // State cho b lc Loi (gi li v c th c trng species lng nhau)
  const [selectedSpecies, setSelectedSpecies] = useState('');
  // State cho b lc Loi c s ng k (C s gy nui / C s kinh doanh, ch bin g)
  // Mc nh l "ng k c s kinh doanh, ch bin g"
  const [selectedLoaiCoSoDangKy, setSelectedLoaiCoSoDangKy] = useState('ng k c s kinh doanh, ch bin g');
  // State cho b lc Ngnh ngh kinh doanh g
  const [selectedNganhNgheKinhDoanhGo, setSelectedNganhNgheKinhDoanhGo] = useState('');
  // State cho b lc Trng thi
  const [selectedTrangThai, setSelectedTrangThai] = useState(''); // Thm state cho lc Trng thi

  // State  lu tr cc gi tr Tnh (Thnh ph) duy nht cho dropdown
  const [uniqueProvinces, setUniqueProvinces] = useState([]);
  // State  lu tr cc gi tr X (Phng) duy nht cho dropdown
  const [uniqueCommunes, setUniqueCommunes] = useState([]);
  // State  lu tr cc gi tr Loi duy nht cho dropdown
  const [uniqueSpecies, setUniqueSpecies] = useState([]);
  // State  lu tr cc gi tr Loi c s ng k duy nht cho dropdown
  const [uniqueLoaiCoSoDangKy, setUniqueLoaiCoSoDangKy] = useState([]);
  // State  lu tr cc gi tr Ngnh ngh kinh doanh g duy nht cho dropdown
  const [uniqueNganhNgheKinhDoanhGo, setUniqueNganhNgheKinhDoanhGo] = useState([]);
  // State  lu tr cc gi tr Trng thi duy nht cho dropdown
  const [uniqueTrangThai, setUniqueTrangThai] = useState([]); // Thm state cho unique Trng thi

  // Hook  iu hng trang
  const navigate = useNavigate();
  // Ly token xc thc t localStorage
  const token = localStorage.getItem('token');

  // nh ngha cu hnh cc ct ca bng, bao gm ID, nhn hin th, trng thi hin th ban u, v chiu rng ti thiu
  // Cc trng khng c lit k  y s khng hin th v khng c ty chn n/hin
  const initialColumnsConfig = {
    // Thng tin c s (ch hin th cc trng yu cu)
	tinhThanhPho: { id: 'tinhThanhPho', label: 'Tnh (TP)', visible: true, minWidth: '130px' },
	xaPhuong: { id: 'xaPhuong', label: 'X (Phng)', visible: true, minWidth: '150px' },
    diaChiCoSo: { id: 'diaChiCoSo', label: 'a ch c s', visible: true, minWidth: '250px' },
    tenCoSo: { id: 'tenCoSo', label: 'Tn c s', visible: true, minWidth: '250px' },
    
    // Ngi i din (ch hin th cc trng yu cu)
    tenNguoiDaiDien: { id: 'tenNguoiDaiDien', label: 'Ngi i din', visible: true, minWidth: '180px' },
    
    // Cc trng chung khc (ch hin th cc trng yu cu)
    trangThai: { id: 'trangThai', label: 'Trng thi', visible: true, minWidth: '100px' },
        
    // Ct Bn  (lun hin th)
    mapLink: { id: 'mapLink', label: 'Bn ', visible: true, minWidth: '10px' }, // Adjusted minWidth from '5px' to '80px'
  };

  // State  qun l trng thi hin th ca cc ct
  const [columns, setColumns] = useState(initialColumnsConfig);
  // State  qun l trng thi hin th ca bng ty chn ct
  const [showColumnOptions, setShowColumnOptions] = useState(false);

  // Cc state lin quan n phn trang
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(15); // Mc nh 15 mc mi trang

  /**
   * useEffect  ti trng thi hin th ct t localStorage khi component c mount.
   * iu ny gip duy tr la chn ca ngi dng gia cc ln truy cp.
   */
  useEffect(() => {
    try {
      // S dng mt key ring cho localStorage  trnh xung t vi cc trang khc
      const savedColumns = localStorage.getItem('tableColumnsVisibility_GoogleMaps'); 
      if (savedColumns) {
        const parsedSavedColumns = JSON.parse(savedColumns);
        // Hp nht cu hnh  lu vi cu hnh ban u  x l cc ct mi/b xa
        const mergedColumns = Object.keys(initialColumnsConfig).reduce((acc, key) => {
          const savedCol = parsedSavedColumns.find(sCol => sCol.id === key);
          // Hp nht trng thi hin th  lu, nhng vn gi cc thuc tnh ban u (nh width, minWidth)
          acc[key] = savedCol ? { ...initialColumnsConfig[key], visible: savedCol.visible } : initialColumnsConfig[key];
          return acc;
        }, {});
        setColumns(mergedColumns);
      }
    } catch (error) {
      console.error("Li khi ti trng thi hin th ct t localStorage:", error);
      // Nu c li khi ti, fallback v cu hnh ct ban u
      setColumns(initialColumnsConfig);
    }
  }, []); // Mng dependency rng m bo useEffect ny ch chy mt ln khi mount

  /**
   * useEffect  lu trng thi hin th ct vo localStorage mi khi state 'columns' thay i.
   */
  useEffect(() => {
    try {
      // Ch lu id v trng thi hin th  trnh lu tr cc thuc tnh CSS khng cn thit
      const columnsToSave = Object.keys(columns).map(key => ({
        id: key,
        visible: columns[key].visible
      }));
      localStorage.setItem('tableColumnsVisibility_GoogleMaps', JSON.stringify(columnsToSave)); // S dng key ring
    } catch (error) {
      console.error("Li khi lu trng thi hin th ct vo localStorage:", error);
    }
  }, [columns]); // Chy mi khi state 'columns' thay i


  /**
   * useEffect  ti danh sch c s t API khi component c mount
   * hoc khi cc b lc `selectedLoaiCoSoDangKy` hoc `selectedTrangThai` thay i.
   */
  useEffect(() => {
    const fetchFarms = async () => {
      try {
        const params = {};
        // Thm b lc farmType vo params nu `selectedLoaiCoSoDangKy` c chn
        if (selectedLoaiCoSoDangKy !== '') {
          params.farmType = selectedLoaiCoSoDangKy;
        }
        // Thm b lc trng thi vo params nu `selectedTrangThai` c chn
        if (selectedTrangThai !== '') {
          params.trangThai = selectedTrangThai;
        }

        // Gi yu cu GET n API  ly danh sch c s
        const response = await axios.get('http://localhost:10000/api/farms', {
          headers: {
            Authorization: `Bearer ${token}` // Gi token xc thc trong header
          },
          params: params // Truyn cc tham s b lc vo backend
        });
        
        // CHNH SA TY Y: Truy cp vo thuc tnh .docs ca response.data
        const fetchedFarms = response.data.docs || []; 
        setFarms(fetchedFarms); // Cp nht state farms vi d liu nhn c

        // Trch xut cc gi tr duy nht cho cc b lc t d liu  ti
        const provinces = [...new Set(fetchedFarms.map(f => f.tinhThanhPho).filter(Boolean))].sort();
        const communes = [...new Set(fetchedFarms.map(f => f.xaPhuong).filter(Boolean))].sort();
        // Gi s 'species' l mt mng lng nhau ca cc i tng c thuc tnh 'name'
        const species = [...new Set(fetchedFarms.flatMap(f => f.species ? f.species.map(s => s.name) : []).filter(Boolean))].sort();
        const loaiCoSoDangKy = [...new Set(fetchedFarms.map(f => f.loaiCoSoDangKy).filter(Boolean))].sort();
        const nganhNgheKinhDoanhGo = [...new Set(fetchedFarms.map(f => f.nganhNgheKinhDoanhGo).filter(Boolean))].sort();
        const trangThai = [...new Set(fetchedFarms.map(f => f.trangThai).filter(Boolean))].sort(); // Ly cc gi tr trng thi duy nht

        // Cp nht cc state cha cc gi tr duy nht
        setUniqueProvinces(provinces);
        setUniqueCommunes(communes);
        setUniqueSpecies(species);
        setUniqueLoaiCoSoDangKy(loaiCoSoDangKy);
        setUniqueNganhNgheKinhDoanhGo(nganhNgheKinhDoanhGo);
        setUniqueTrangThai(trangThai);

      } catch (error) {
        console.error(' Li khi ly danh sch c s:', error);
        // Nu li do xc thc (401) hoc khng c y quyn (403), chuyn hng n trang ng nhp
        if (error.response && (error.response.status === 401 || error.response.status === 403)) {
          navigate('/');
        }
        setFarms([]); // m bo farms l mng rng khi c li
      }
    };

    // Ch ti d liu nu token tn ti
    if (token) {
      fetchFarms();
    } else {
      navigate('/'); // Chuyn hng nu khng c token
    }
  }, [token, navigate, selectedLoaiCoSoDangKy, selectedTrangThai]); // Re-fetch khi token, navigate, selectedLoaiCoSoDangKy hoc selectedTrangThai thay i

  /**
   * Hm iu hng n Google Maps vi ta  ca c s.
   * @param {string} lat - V  (latitude) ca c s (WGS84).
   * @param {string} lon - Kinh  (longitude) ca c s (WGS84).
   */
  const handleNavigateToMap = (lat, lon) => {
    if (lat && lon) {
      const mapUrl = `https://www.google.com/maps/search/?api=1&query=$${lat},${lon}`;
      window.open(mapUrl, '_blank'); // M bn  trong tab mi
    } else {
      alert('Khng c thng tin v  hoc kinh  cho c s ny.'); // Thay bng modal thng bo ty chnh
    }
  };

  /**
   * Lc danh sch c s da trn tt c cc tiu ch (lc frontend cho cc trng khc).
   */
  const filteredFarms = farms.filter(f => {
    // Kim tra khp vi b lc tm kim chung
    const generalMatch = (
      (f.tenCoSo || '').toLowerCase().includes(filter.toLowerCase()) ||
      (f.tinhThanhPho || '').toLowerCase().includes(filter.toLowerCase()) ||
      (f.xaPhuong || '').toLowerCase().includes(filter.toLowerCase()) ||
      (f.diaChiCoSo || '').toLowerCase().includes(filter.toLowerCase()) ||
      (f.ghiChu || '').toLowerCase().includes(filter.toLowerCase()) || 
      (f.loaiCoSoDangKy || '').toLowerCase().includes(filter.toLowerCase()) || 
      (f.nganhNgheKinhDoanhGo || '').toLowerCase().includes(filter.toLowerCase()) || 
      (f.tongDan && String(f.tongDan).toLowerCase().includes(filter.toLowerCase())) || 
      (f.khoiLuong && String(f.khoiLuong).toLowerCase().includes(filter.toLowerCase())) || 
      (f.tenLamSan && String(f.tenLamSan).toLowerCase().includes(filter.toLowerCase())) || 
      (f.tenKhoaHoc && String(f.tenKhoaHoc).toLowerCase().includes(filter.toLowerCase())) || 
      (f.trangThai && String(f.trangThai).toLowerCase().includes(filter.toLowerCase())) ||
      (f.tenNguoiDaiDien || '').toLowerCase().includes(filter.toLowerCase()) ||
      (f.namSinh && String(f.namSinh).toLowerCase().includes(filter.toLowerCase())) || 
      (f.soCCCD && String(f.soCCCD).toLowerCase().includes(filter.toLowerCase())) || 
      (f.ngayCapCCCD && new Date(f.ngayCapCCCD).toLocaleDateString().toLowerCase().includes(filter.toLowerCase())) ||
      (f.noiCapCCCD && String(f.noiCapCCCD).toLowerCase().includes(filter.toLowerCase())) || 
      (f.soDienThoaiNguoiDaiDien && String(f.soDienThoaiNguoiDaiDien).toLowerCase().includes(filter.toLowerCase())) ||
      (f.diaChiNguoiDaiDien && String(f.diaChiNguoiDaiDien).toLowerCase().includes(filter.toLowerCase())) 
    );

    // Kim tra khp vi b lc Tnh (Thnh ph)
    const provinceMatch = selectedProvince ? (f.tinhThanhPho === selectedProvince) : true;
    // Kim tra khp vi b lc X (Phng)
    const communeMatch = selectedCommune ? (f.xaPhuong === selectedCommune) : true;
    // Kim tra khp vi b lc Loi (nu c trng species)
    const speciesMatch = selectedSpecies ? (f.species && f.species.some(s => s.name === selectedSpecies)) : true;
    // `nganhNgheKinhDoanhGoMatch` hin c x l bi b lc backend, nhng vn gi li  nht qun nu cn cho cc b lc khc
    const nganhNgheKinhDoanhGoMatch = selectedNganhNgheKinhDoanhGo ? (f.nganhNgheKinhDoanhGo === selectedNganhNgheKinhDoanhGo) : true;
    // `trangThaiMatch` hin c x l bi b lc backend, nhng vn gi li  nht qun nu cn cho cc b lc khc
    const trangThaiMatch = selectedTrangThai ? (f.trangThai === selectedTrangThai) : true;


    // Tr v true nu tt c cc iu kin b lc u khp
    return generalMatch && provinceMatch && communeMatch && speciesMatch && nganhNgheKinhDoanhGoMatch && trangThaiMatch;
  });

  // Logic phn trang
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = filteredFarms.slice(indexOfFirstItem, indexOfLastItem);

  const totalPages = Math.ceil(filteredFarms.length / itemsPerPage);

  // Hm thay i trang hin ti
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  // Hm chuyn n trang tip theo
  const nextPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage(currentPage + 1);
    }
  };

  // Hm chuyn n trang trc
  const prevPage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  /**
   * Hm x l vic bt/tt hin th ct.
   * @param {string} columnKey - Kha ca ct cn thay i trng thi hin th.
   */
  const handleColumnToggle = (columnKey) => {
    setColumns(prevColumns => ({
      ...prevColumns,
      [columnKey]: {
        ...prevColumns[columnKey],
        visible: !prevColumns[columnKey].visible // o ngc trng thi hin th ca ct
      }
    }));
  };

  /**
   * Hm chn tt c cc ct hin th.
   */
  const handleSelectAllColumns = () => {
    setColumns(prevColumns => {
      const newColumns = { ...prevColumns };
      for (const key in newColumns) {
        newColumns[key].visible = true;
      }
      return newColumns;
    });
  };

  

  return (
    <div className="farm-list-container"> {/* Container chnh ca trang danh sch */}
    

      <div className="filter-container"> {/* Container cho cc b lc */}
        <input
          type="text"
          placeholder=" Tm kim chung..."
          value={filter}
          onChange={e => setFilter(e.target.value)}
        />

        {/* B lc mi cho Loi c s ng k */}
        <select
          value={selectedLoaiCoSoDangKy}
          onChange={e => setSelectedLoaiCoSoDangKy(e.target.value)}
        >
          <option value="">Tt c c s</option>
          <option value="ng k c s gy nui">ng k c s gy nui</option>
          <option value="ng k c s kinh doanh, ch bin g">ng k c s kinh doanh, ch bin g</option>
        </select>

        <select
          value={selectedProvince}
          onChange={e => setSelectedProvince(e.target.value)}
        >
          <option value="">Tt c Tnh (TP)</option>
          {uniqueProvinces.map(province => (
            <option key={province} value={province}>{province}</option>
          ))}
        </select>

        <select
          value={selectedCommune}
          onChange={e => setSelectedCommune(e.target.value)}
        >
          <option value="">Tt c X (Phng)</option>
          {uniqueCommunes.map(commune => (
            <option key={commune} value={commune}>{commune}</option>
          ))}
        </select>

        <select
          value={selectedSpecies}
          onChange={e => setSelectedSpecies(e.target.value)}
        >
          <option value="">Tt c Loi nui</option>
          {uniqueSpecies.map(species => (
            <option key={species} value={species}>{species}</option>
          ))}
        </select>

        {/* B lc mi cho Ngnh ngh kinh doanh g */}
        <select
          value={selectedNganhNgheKinhDoanhGo}
          onChange={e => setSelectedNganhNgheKinhDoanhGo(e.target.value)}
        >
          <option value="">Tt c Ngnh ngh KD g</option>
          {uniqueNganhNgheKinhDoanhGo.map(nganhNghe => (
            <option key={nganhNghe} value={nganhNghe}>{nganhNghe}</option>
          ))}
        </select>

        {/* B lc mi cho Trng thi */}
        <select
          value={selectedTrangThai}
          onChange={e => setSelectedTrangThai(e.target.value)}
        >
          <option value="">Tt c Trng thi</option>
          {uniqueTrangThai.map(status => (
            <option key={status} value={status}>{status}</option>
          ))}
        </select>


      </div>

      

      {/* Hin th thng bo khi khng c c s no c ng k */}
      {farms.length === 0 && !filter && <p>Khng c c s nui no c ng k.</p>}
      {farms.length === 0 && filter && <p>Khng tm thy c s nui no ph hp vi b lc.</p>}

      {/* Bng hin th danh sch c s nu c d liu */}
      {filteredFarms.length > 0 && (
        <table border="1" cellPadding="8" className="table-auto w-full border-collapse">
          <thead style={{ background: '#f0f0f0' }}>
            <tr>
              {Object.keys(columns).map(key => (
                <th
                  key={key}
                  className={!columns[key].visible ? 'column-hidden' : ''}
                  style={
                    key === 'actions'
                      ? { // Ct hnh ng: chiu rng c nh
                          width: columns[key].width,
                          minWidth: columns[key].minWidth,
                          maxWidth: columns[key].maxWidth,
                        }
                      : (columns[key].visible
                          ? { // Cc ct khng phi hnh ng ang hin th: s dng minWidth lm chiu rng
                              width: columns[key].minWidth, // t chiu rng bng minWidth
                              minWidth: columns[key].minWidth, // m bo minWidth c tn trng
                              maxWidth: columns[key].maxWidth || 'auto', // Cho php m rng nu c khng gian, nhng gi ellipsis
                            }
                          : {}
                        )
                  }
                >
                  {columns[key].label}
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {currentItems.map((f, i) => (
              <tr key={f._id || i}>
                {Object.keys(columns).map(key => (
                  <td
                    key={`${f._id || i}-${key}`}
                    className={!columns[key].visible ? 'column-hidden' : ''}
                    style={
                      key === 'actions'
                        ? { // Ct hnh ng: chiu rng c nh
                            width: columns[key].width,
                            minWidth: columns[key].minWidth,
                            maxWidth: columns[key].maxWidth,
                          }
                        : (columns[key].visible
                            ? { // Cc ct khng phi hnh ng ang hin th: s dng minWidth lm chiu rng
                                width: columns[key].minWidth, // t chiu rng bng minWidth
                                minWidth: columns[key].minWidth, // m bo minWidth c tn trng
                                maxWidth: columns[key].maxWidth || 'auto', // Cho php m rng nu c khng gian, nhng gi ellipsis
                              }
                            : {}
                          )
                    }
                  >
                    {/* X l ni dung ca  */}
                    {key === 'mapLink' ? (
                      <button onClick={() => handleNavigateToMap(f.vido, f.kinhdo)} className="action-button view-button">
                        M
                      </button>
                    ) : (
                      // Cc trng ngy thng
                      ['ngayThanhLap', 'ngayCapCCCD'].includes(key) ?
                        (f[key] ? new Date(f[key]).toLocaleDateString() : '') :
                        f[key]
                    )}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      )}

      {/* iu khin phn trang */}
      {filteredFarms.length > 0 && (
        <div className="pagination-container">
          <div className="pagination-info">
            {indexOfFirstItem + 1} - {Math.min(indexOfLastItem, filteredFarms.length)} / {filteredFarms.length} bn ghi
          </div>
          <div className="pagination-controls">
            <button onClick={prevPage} disabled={currentPage === 1} className="pagination-button"></button>
            {Array.from({ length: totalPages }, (_, i) => (
              <button
                key={i + 1}
                onClick={() => paginate(i + 1)}
                className={`pagination-button ${currentPage === i + 1 ? 'active' : ''}`}
              >
                {i + 1}
              </button>
            ))}
            <button onClick={nextPage} disabled={currentPage === totalPages} className="pagination-button"></button>
          </div>
          <div className="items-per-page">
            <select value={itemsPerPage} onChange={(e) => {
              setItemsPerPage(Number(e.target.value));
              setCurrentPage(1); // t li v trang u tin khi s mc mi trang thay i
            }}>
              <option value="5">5 bn ghi/trang</option>
              <option value="10">10 bn ghi/trang</option>
              <option value="15">15 bn ghi/trang</option>
              <option value="20">20 bn ghi/trang</option>
              <option value="50">50 bn ghi/trang</option>
            </select>
          </div>
        </div>
      )}
    </div>
  );
}

export default FarmListPage;