import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

// Nhn prop setAuthStatus t App.js
function LogoutPage({ setAuthStatus }) {
  const navigate = useNavigate();

  useEffect(() => {
    // Xa token v vai tr khi localStorage
    localStorage.removeItem('token');
    localStorage.removeItem('role');
    
    // Reset trng thi token v role trong App.js
    if (setAuthStatus) { // m bo prop tn ti
      setAuthStatus(null, null); // Set token v role v null
    }

    // Chuyn hng v trang ng nhp sau khi ng xut
    navigate('/');
  }, [navigate, setAuthStatus]); // Thm setAuthStatus vo dependency array

  return (
    <div>
      <p>ang ng xut...</p>
    </div>
  );
}

export default LogoutPage;