import React, { useState, useEffect, useMemo } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './MasterProductListPage.css';

function MasterProductListPage() {
    const navigate = useNavigate();
    const [masterData, setMasterData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [filters, setFilters] = useState({
        tenCoSo: '',
        tinhThanhPho: '',
        tenLamSan: '',
        trangThai: ''
    });

    useEffect(() => {
        const token = localStorage.getItem('token');
        if (!token) {
            navigate('/login');
        } else {
            fetchMasterData(token);
        }
    }, [navigate]);

    const fetchMasterData = async (token) => {
        setLoading(true);
        try {
            const response = await axios.get('http://localhost:10000/api/master-product-list', {
                headers: { 'Authorization': `Bearer ${token}` }
            });
            setMasterData(response.data);
        } catch (err) {
            console.error("Li khi gi API:", err.response || err.message);
        } finally {
            setLoading(false);
        }
    };

    // Hm c nh ngha vi tn 'handleFilterChange' (ch C hoa)
    const handleFilterChange = (e) => {
        const { name, value } = e.target;
        setFilters(prev => ({ ...prev, [name]: value }));
    };

    const filteredData = useMemo(() => {
        return masterData.filter(item => {
            return (
                (filters.tenCoSo ? item.tenCoSo?.toLowerCase().includes(filters.tenCoSo.toLowerCase()) : true) &&
                (filters.tinhThanhPho ? item.tinhThanhPho?.toLowerCase().includes(filters.tinhThanhPho.toLowerCase()) : true) &&
                (filters.tenLamSan ? item.tenLamSan?.toLowerCase().includes(filters.tenLamSan.toLowerCase()) : true) &&
                (filters.trangThai ? item.trangThai === filters.trangThai : true)
            );
        });
    }, [filters, masterData]);

    if (loading) {
        return <div className="master-list-container"><p>ang ti d liu, vui lng ch...</p></div>;
    }

    return (
        <div className="master-list-container">
            <h1>Bng tng hp Lm sn v C s</h1>

            <div className="filters-panel">
                {/* ===  SA LI CHNH T  Y === */}
                <input type="text" name="tenCoSo" placeholder="Lc theo tn c s..." onChange={handleFilterChange} value={filters.tenCoSo} />
                <input type="text" name="tinhThanhPho" placeholder="Lc theo tnh (TP)..." onChange={handleFilterChange} value={filters.tinhThanhPho} />
                <input type="text" name="tenLamSan" placeholder="Lc theo tn lm sn..." onChange={handleFilterChange} value={filters.tenLamSan} />
                <select name="trangThai" onChange={handleFilterChange} value={filters.trangThai}>
                    <option value="">Tt c trng thi</option>
                    <option value="ang hot ng">ang hot ng</option>
                    <option value=" ng ca"> ng ca</option>
                    <option value="Tm dng">Tm ngng</option>
                </select>
            </div>

            <div className="table-wrapper">
                <table className="master-table">
                    <thead>
                        <tr>
                            <th>Tn Lm sn</th>
                            <th>Khi lng</th>
                            <th>n v</th>
                            <th>Ngun gc G</th>
                            <th>Ngy ng k</th>
                            <th>Tn C s</th>
                            <th>Tnh (TP)</th>
                            <th>a ch</th>
                            <th>Trng thi</th>
                            <th>Ngi i din</th>
                            <th>S CCCD</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredData.length > 0 ? filteredData.map(item => (
                            <tr key={item._id || item.farmId + item.tenLamSan}>
                                <td className="highlight-col">{item.tenLamSan}</td>
                                <td>{item.khoiLuong}</td>
                                <td>{item.donViTinh}</td>
                                <td>{item.nguonGocGo}</td>
                                <td>{item.ngayDangKyLamSan ? new Date(item.ngayDangKyLamSan).toLocaleDateString() : ''}</td>
                                <td className="highlight-col">{item.tenCoSo}</td>
                                <td>{item.tinhThanhPho}</td>
                                <td>{item.diaChiCoSo}</td>
                                <td>{item.trangThai}</td>
                                <td>{item.tenNguoiDaiDien}</td>
                                <td>{item.soCCCD}</td>
                            </tr>
                        )) : (
                            <tr>
                                <td colSpan="11" style={{ textAlign: 'center' }}>Khng tm thy d liu ph hp.</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default MasterProductListPage;