import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './Manager.css';

function UserListPage() {
  const [users, setUsers] = useState([]);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState('staff');
  const [editingUser, setEditingUser] = useState(null); // State  lu ngi dng ang chnh sa
  const token = localStorage.getItem('token');

  const fetchUsers = async () => {
    try {
      const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/users`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      setUsers(res.data);
    } catch (error) {
      alert('Khng th ti danh sch ngi dng: ' + (error.response?.data?.message || error.message));
    }
  };

  useEffect(() => { fetchUsers(); }, []);

  const handleAddUser = async (e) => {
    e.preventDefault();
    try {
      await axios.post(`${process.env.REACT_APP_API_APP_URL}/api/users`, {
        email, password, role
      }, {
        headers: { Authorization: `Bearer ${token}` },
      });
      setEmail(''); setPassword(''); setRole('staff');
      fetchUsers();
      alert('Thm ngi dng thnh cng!');
    } catch (error) {
      alert('Li khi thm ngi dng: ' + (error.response?.data?.message || error.message));
    }
  };

  const handleEditClick = (user) => {
    setEditingUser(user);
    setEmail(user.email);
    setPassword(''); // Mt khu khng c hin th  chnh sa trc tip
    setRole(user.role);
  };

  const handleUpdateUser = async (e) => {
    e.preventDefault();
    if (!editingUser) return;

    try {
      const updateData = { email, role };
      if (password) { // Ch gi mt khu nu c s thay i
        updateData.password = password;
      }

      await axios.put(`${process.env.REACT_APP_API_URL}/api/users/${editingUser._id}`, updateData, {
        headers: { Authorization: `Bearer ${token}` },
      });
      setEditingUser(null);
      setEmail(''); setPassword(''); setRole('staff');
      fetchUsers();
      alert('Cp nht ngi dng thnh cng!');
    } catch (error) {
      alert('Li khi cp nht ngi dng: ' + (error.response?.data?.message || error.message));
    }
  };

  const handleDeleteUser = async (userId) => {
    if (window.confirm('Bn c chc chn mun xa ngi dng ny?')) {
      try {
        await axios.delete(`${process.env.REACT_APP_API_URL}/api/users/${userId}`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        fetchUsers();
        alert('Xa ngi dng thnh cng!');
      } catch (error) {
        alert('Li khi xa ngi dng: ' + (error.response?.data?.message || error.message));
      }
    }
  };

  return (
    <div className="manager-container">
      <h2> Qun l ngi dng</h2>
      
      {/* Form thm hoc chnh sa ngi dng */}
      <form onSubmit={editingUser ? handleUpdateUser : handleAddUser}>
        <h3>{editingUser ? 'Chnh sa ngi dng' : 'Thm ngi dng mi'}</h3>
        <input type="email" placeholder="Email" value={email} onChange={e => setEmail(e.target.value)} required />
        <input 
          type="password" 
          placeholder={editingUser ? "Mt khu mi ( trng nu khng i)" : "Mt khu"} 
          value={password} 
          onChange={e => setPassword(e.target.value)} 
          required={!editingUser} // Bt buc nhp khi thm mi, khng bt buc khi chnh sa
        />
        <select value={role} onChange={e => setRole(e.target.value)}>
          <option value="staff">Nhn vin</option>
          <option value="admin">Qun tr vin</option>
        </select>
        <button type="submit">{editingUser ? 'Cp nht ngi dng' : ' Thm ngi dng'}</button>
        {editingUser && (
          <button type="button" onClick={() => {
            setEditingUser(null);
            setEmail(''); setPassword(''); setRole('staff');
          }}>Hy b</button>
        )}
      </form>

      <table>
        <thead>
          <tr>
            <th>Email</th>
            <th>Vai tr</th>
            <th>Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          {users.map(u => (
            <tr key={u._id}>
              <td>{u.email}</td>
              <td>{u.role}</td>
              <td>
                <button onClick={() => handleEditClick(u)}>Sa</button>
                <button onClick={() => handleDeleteUser(u._id)}>Xa</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default UserListPage;